import CRUDComponent from "../CRUDComponent/CRUDComponent";
import TextAreaComponent from "../CRUDComponent/TextAreaComponent";
import s from "./../../Profilestyles.module.css";

type PostsType = {
  openPostFn: () => void;
  addShorts: () => void;
  closePostFn: () => void;
  someValue: (value: string) => void;
  someShorts: (value: string) => void;
  value: string;
  valueShorts: string;
  shorts: boolean;
  post: boolean;
};

export const PostsComponentHOC = ({ openPostFn, addShorts, closePostFn, someShorts, value, valueShorts, someValue, shorts, post }: PostsType) => {
  return (
    <article className={s.profileInfo__posts}>
      <CRUDComponent addPost={openPostFn} addShorts={addShorts} />
      {post && (
        <TextAreaComponent accept={"image/*,.png,.jpg,.gif,.web,.webp"} closeFn={closePostFn} value={value} onChange={(str) => someValue(str)} />
      )}
      {shorts && (
        <TextAreaComponent
          accept={"video/mp4,video/x-m4v,video/*"}
          closeFn={closePostFn}
          value={valueShorts}
          onChange={(str) => someShorts(str)}
          shorts={true}
        />
      )}
      {/* {pinnedPost} */}
      {/* <PostComponent
    name={data.fullName}
    photo={data.photos === undefined || null ? DefaultIcon : photosLarge(data.photos.large)
    post={postsCount}
    setA={openCloseMoreBtn}
  /> */}
    </article>
  );
};
